.toggle{
    display: flex;
    justify-content: space-between;
    border: 3px solid var(--blue);
    border-radius: 1rem;
    position: relative;
    padding: 2px;
    cursor: pointer;
}

.toggle>*{
    width: 1.5rem;
    height: 1rem;
    color: var(--blue);
}

.t-button{
    border-radius: 100%;
    position: absolute;
    background: var(--blue);
}