.n-wrapper{
    height:12vh;
    display:flex;
    justify-content: space-between;
}

.n-left{
    flex:1;
    align-items: center;
    display:flex;
    gap: 2rem;
}

.n-name{
    font-size: 1.3rem;
    font-weight: bold;
}

.n-right{
    display: flex;
    justify-content: center;
    align-items: center;
    flex:1;
    font-weight:400;
}

/*navbar list*/
.n-list{
    flex:10;
    
}

.n-list>ul{
    display:flex;
    gap:2rem;
    margin-right: 4rem;
    list-style-type: none;
    cursor: pointer;
}  

.n-list>ul>li:hover{
    cursor:pointer;
    color: var(--lgblue);
}

.n-button{
    flex:1;
}

@media screen and (max-width: 480px){
  .n-list{
    display: none;
  }
  .n-button{
    display: none;
  }
  
}